/* src/App.css */
body {
    font-family: Arial, sans-serif;
    margin: 20px;
}

.app {
    text-align: center;
}

h1 {
    color: #333;
}

input {
    margin: 10px;
    padding: 5px;
    font-size: 1em;
}

button {
    padding: 10px 20px;
    margin: 10px;
    font-size: 1em;
    cursor: pointer;
}

.section, .filter {
    margin: 20px;
}

.fields, .tags {
    margin-top: 10px;
}

